.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 30px;
}

.App-title {
  font-size: 1.5em;
}

.list_container {
  width: 100%;
}

.list_wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  height: calc(100vh - 93px);
  overflow-y: auto;
  width: 100%;
}

.cardItemVacancies {
  border: 1px dashed black;
  border-radius: 10px;
  width: 23.5%;
  margin: 10px;
  text-align: center;
  background: #f3f3f3;
}

.cardItemVacancies div {
  font-size: 14px;
  padding: 5px;
}

.vacancy-text {
  font-weight: bold;
}


